import React, { useState, forwardRef } from "react";
import { useLoading } from "../utils/LoadingContext";
import {Book, songIndex } from "../types";
import {useLocalStorageCache, doesKeyExistInLocalStorage} from "../utils/useLocalStorageCache";
import { useSwipeable  } from 'react-swipeable';
import { useSongIndexContext } from '../appobjects/contexts/SongIndexContext'; 

interface SongIndexProps {
  selectedBook: Book | null;
  isVisible:Boolean;
  songIndices:songIndex[]|[]
  onSelect: (songindex: songIndex) => void;
  currentSongPosition: number | null; // Add this prop
  onSwipeLeft:Function
  onSwipeRight:Function
}

export const SongIndices = forwardRef<HTMLDivElement, SongIndexProps>(({  isVisible, selectedBook,songIndices,onSelect,currentSongPosition,onSwipeLeft,onSwipeRight }, ref) => {
  //const { songIndices, selectedBook } = useSongIndexContext();
  const { showLoading, hideLoading } = useLoading();
  const [error, setError] = useState<string | null>(null);
  const localStorageKey:string = selectedBook && selectedBook.bookId ? `songIndices-${selectedBook.bookId}` : 'songIndices-no-book'; // Default key
  //console.log("localStorageKey:"+localStorageKey);
  //const { storedValue: cachedSongIndices, setValue: setCachedSongIndices } = useLocalStorageCache<songIndex[]>(localStorageKey);

  const handleSwipeLeftLocal = () => {
    console.log("SongIndices Swipe Left - Go Back (Local)");
    // Your global back navigation logic here
    onSwipeLeft()
  };

  const handleSwipeRightLocal = () => {
    console.log("SongIndices Swipe Right - Go To Last Song (Local)");
    // Your global go to last song logic here
    onSwipeRight()
  };

  const swipeHandlersGlobal = useSwipeable({
    onSwipedLeft:handleSwipeRightLocal,
    onSwipedRight: handleSwipeLeftLocal,
    trackMouse: true,
    swipeDuration: 500,
    delta: 250,
  });


  const isSongVerseInCache = (bookId: string | undefined, songTitle: string) => {
    try {
      if (!bookId) return false;
      const localStorageKey = `songVerses-${bookId}-${songTitle}`;
      return doesKeyExistInLocalStorage(localStorageKey);
    } catch (error) {
      console.error('Error checking local storage:', error);
      return false;
    }
  };

  return (
    //<div ref={ref} style={{ display: isVisible  ? "block" : "none", overflowY: 'auto',height: '80dvh'}}>
    //<div ref={ref} style={{ display: isVisible  ? "block" : "none"}} {...swipeHandlersGlobal}>
    <div style={{ display: isVisible  ? "block" : "none"}} {...swipeHandlersGlobal}>
      <h2 className="text-xl font-semibold mb-4">
        Songs from Book ({selectedBook?.bookName}) 
      </h2>
      <div className="space-y-3">
        {songIndices &&
          songIndices.map((songindex,index) => (
            <div
              key={songindex.sourceRow} id={`songindex-item-${index}`}
              className={`
                bg-white p-4 rounded-lg shadow flex items-center justify-between cursor-pointer hover:bg-indigo-50
                ${currentSongPosition === index ? 'bg-yellow-100 border border-yellow-400' : ''}
              `}
              onClick={() => onSelect(songindex)}
            >
              <div>
                <h3 className="font-medium" 
                style={{
                  color: isSongVerseInCache(selectedBook?.bookId, songindex.songTitle) ? 'green' : 'black',
                }}
                >{songindex.songTitle}</h3>
                <p className="text-gray-500 text-sm">{songindex.verseCount} # of verses</p>
              </div>
              <span className="text-indigo-600">→</span>
            </div>
          ))}
      </div>
    </div>
  );
});
