import React, { useState, useEffect, forwardRef } from "react";
import { useLoading } from "../utils/LoadingContext";
import { Category, Book } from "../types";
import { GOOGLE_WEBAPP_URL } from "../utils/Constants";
import {
  useLocalStorageCache,
  doesKeyExistInLocalStorage,
} from "../utils/useLocalStorageCache";

const useCategoryAndBookData = () => {
  const [categoriesAndBooks, setCategoriesAndBooks] = useState<Category[]>([]);
  const {
    storedValue: cachedCategoriesAndBooks,
    setValue: setCachedCategoriesAndBooks,
  } = useLocalStorageCache<Category[]>("categoriesAndBooks");

  const { showLoading, hideLoading } = useLoading();
  const [error, setError] = useState<string | null>(null);
  const processApiResponseBooksAndCategories = (
    rawData: Record<string, any[]>
  ): Category[] => {
    //console.log('in processApiResponseBooksAndCategories... should be only once on init browser page')
    return Object.entries(rawData).map(([categoryName, books]) => {
      // Transform books array
      const processedBooks: Book[] = books.map((book) => ({
        bookId: book["Book Id"],
        bookName: book["Book Name"],
        songCount: book["Song Count"],
        verseCount: book["Verse Count"],
        isBookSourceSupabase: Boolean(book["isBookSourceSupabase"]), // Convert to boolean
      }));

      return {
        name: categoryName,
        bookCount: processedBooks.length,
        books: processedBooks,
        image: `https://placehold.co/800/orange/black?text=${categoryName}&font=lora`,
      };
    });
  };

  useEffect(() => {
    const abortControllerBooksGWebApp = new AbortController();
    const fetchCategoriesAndBooksFromGWebApp = async () => {
      try {
        //console.log("categoriesAndBooks:"+JSON.stringify(categoriesAndBooks));
        if (cachedCategoriesAndBooks) {
          setCategoriesAndBooks(cachedCategoriesAndBooks ?? []);
        } else {
          //console.log("Fetching categories and books");
          showLoading();
          setCategoriesAndBooks([]);
          const response = await fetch(
            `${GOOGLE_WEBAPP_URL}?mode=api&apiname=getcategoriesandbooks`,
            { signal: abortControllerBooksGWebApp.signal } // Attach its own signal
          );
          if (!response.ok)
            throw new Error(`Books error: ${response.statusText}`);
          const rawData = await response.json();
          let data = processApiResponseBooksAndCategories(rawData);
          //console.log(data);
          if (!abortControllerBooksGWebApp.signal.aborted) {
            setCategoriesAndBooks(data);
            setCachedCategoriesAndBooks(data);
            hideLoading();
          }
        }
      } catch (err) {
        if (!abortControllerBooksGWebApp.signal.aborted) {
          console.error(err instanceof Error ? err.message : "Unknown error");
          hideLoading();
          setError(err instanceof Error ? err.message : "Unknown error");
        }
      }
    };
    fetchCategoriesAndBooksFromGWebApp();

    // 4. Cleanup: Abort both on unmount
    return () => {
      abortControllerBooksGWebApp.abort();
    };
  }, []);
  return { categoriesAndBooks };
};

interface CategoriesProps {
  isVisible: boolean;
  onSelect: (category: Category) => void;
}

export const Categories: React.FC<CategoriesProps> = ({
  isVisible,
  onSelect,
}) => {
  const { categoriesAndBooks } = useCategoryAndBookData();
  return (
    <div style={{ display: isVisible ? "block" : "none" }}>
      <h2 className="text-xl font-semibold mb-4">Categories</h2>
      <div
        className="grid grid-cols-5 gap-6"
        style={{ overflowY: "auto", height: "80dvh" }}
      >
        {categoriesAndBooks.map((category) => (
          <div
            key={category.name}
            className="bg-white p-4 rounded-lg shadow flex flex-col items-center cursor-pointer hover:bg-indigo-50"
            onClick={() => onSelect(category)}
          >
            <img
              src={category.image}
              alt={category.name}
              className="w-16 h-16 mb-2 rounded-full"
            />
            <h3 className="font-medium text-lg">{category.name}</h3>
            <p className="text-gray-500 text-sm">
              {category.bookCount} book(s)
            </p>
          </div>
        ))}
      </div>
    </div>
  );
};

interface BooksProps {
  selectedCategoriesAndBooks: Category | null;
  isVisible: boolean;
  onSelect: (book: Book) => void;
}

const handleDownload = function (book: Book) {
  return false;
};
const handleAddToReadingList = function (book: Book) {
  return false;
};
const handleShare = function (book: Book) {
  return;
};

const handleDelete = function (book: Book) {
  return;
};

export const Books = forwardRef<HTMLDivElement, BooksProps>(
  ({ selectedCategoriesAndBooks, isVisible, onSelect }, ref) => {
    const isSongIndicesInCache = (book: Book) => {
      try {
        const localStorageKey = `songIndices-${book.bookId}`;
        return doesKeyExistInLocalStorage(localStorageKey);
      } catch (error) {
        console.error("Error checking local storage:", error);
        return false;
      }
    };

    return (
      <div ref={ref} style={{ display: isVisible ? "block" : "none" }}>
        <h2 className="text-xl font-semibold mb-4">
          Books in category ({selectedCategoriesAndBooks?.name})
        </h2>
        <div
          className="space-y-3"
          style={{ overflowY: "auto", height: "80dvh" }}
        >
          {selectedCategoriesAndBooks &&
            selectedCategoriesAndBooks.books.map((book, index) => (
              <div
                key={book.bookId}
                id={`book-item-${index}`}
                className="bg-white p-4 rounded-lg shadow flex items-center justify-between cursor-pointer hover:bg-indigo-50"
                onClick={() => onSelect(book)}
              >
                <div>
                  <h3
                    className="font-medium"
                    style={{
                      color: isSongIndicesInCache(book) ? "green" : "black", // Conditional color
                    }}
                  >
                    {book.bookName}
                  </h3>
                  <p className="text-gray-500 text-sm">
                    {book.songCount} songs/chapters
                  </p>
                  <div className="flex items-center space-x-2 mt-1">
                    {" "}
                    {/* Container for icons */}
                    {/* Example: Download Action */}
                    {true && (
                      <button
                        onClick={(event) => {
                          event.stopPropagation(); // Prevent click from propagating to the parent element
                          handleDownload(book);
                        }}
                        aria-label={`Download ${book.bookName}`}
                        className="relative group"
                      >
                        <svg
                          className="h-5 w-5 text-gray-400 hover:text-indigo-600 cursor-pointer"
                          fill="none"
                          viewBox="0 0 24 24"
                          stroke="currentColor"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth={2}
                            d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m-4-2h16"
                          />
                        </svg>
                        <span className="absolute bottom-full left-1/2 transform -translate-x-1/2 bg-gray-800 text-white text-xs py-1 px-2 rounded-md whitespace-nowrap mb-1 opacity-0 transition-opacity duration-200 group-hover:opacity-100 z-10">
                          Download
                        </span>
                      </button>
                    )}
                    {true && (
                      <button
                        onClick={(event) => {
                          event.stopPropagation(); // Prevent click from propagating to the parent element
                          handleDelete(book);
                        }}
                        aria-label={`Delete ${book.bookName}`}
                        className="relative group"
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          fill="none"
                          viewBox="0 0 24 24"
                          strokeWidth="1.5"
                          stroke="currentColor"
                          className="h-6 w-6"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            d="M15 12H9m8-2V8H7v4m10-4H7m0 0l3-3m0 0l-3-3m-3 3v6a2 2 0 002 2h6a2 2 0 002-2V8z"
                          />
                        </svg>
                        <span className="absolute bottom-full left-1/2 transform -translate-x-1/2 bg-gray-800 text-white text-xs py-1 px-2 rounded-md whitespace-nowrap mb-1 opacity-0 transition-opacity duration-200 group-hover:opacity-100 z-10">
                          Delete
                        </span>
                      </button>
                    )}
                    {/* Example: Add to Reading List Action */}
                    {true && (
                      <button
                        onClick={(event) => {
                          event.stopPropagation(); // Prevent click from propagating
                          handleAddToReadingList(book);
                        }}
                        aria-label={`Add ${book.bookName} to reading list`}
                        className="relative group"
                      >
                        <svg
                          className="h-5 w-5 text-gray-400 hover:text-indigo-600 cursor-pointer"
                          fill="none"
                          viewBox="0 0 24 24"
                          stroke="currentColor"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth={2}
                            d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
                          />
                        </svg>
                        <span className="absolute bottom-full left-1/2 transform -translate-x-1/2 bg-gray-800 text-white text-xs py-1 px-2 rounded-md whitespace-nowrap mb-1 opacity-0 transition-opacity duration-200 group-hover:opacity-100 z-10">
                          Add to reading list
                        </span>
                      </button>
                    )}
                    {/* Example: Share Action */}
                    {true && (
                      <button
                        onClick={(event) => {
                          event.stopPropagation(); // Prevent click from propagating
                          handleShare(book);
                        }}
                        aria-label={`Share ${book.bookName}`}
                        className="relative group"
                      >
                        <svg
                          className="h-5 w-5 text-gray-400 hover:text-indigo-600 cursor-pointer"
                          fill="none"
                          viewBox="0 0 24 24"
                          stroke="currentColor"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth={2}
                            d="M8.684 13.342C8.886 12.938 9 12.5 9 12c0-.5-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l4.5-4.5m0 0l4.5 4.5m-2.25-2.25l2.25 2.25"
                          />
                        </svg>
                        <span className="absolute bottom-full left-1/2 transform -translate-x-1/2 bg-gray-800 text-white text-xs py-1 px-2 rounded-md whitespace-nowrap mb-1 opacity-0 transition-opacity duration-200 group-hover:opacity-100 z-10">
                          Share
                        </span>
                      </button>
                    )}
                  </div>
                </div>
                <span className="text-indigo-600">→</span>
              </div>
            ))}
        </div>
      </div>
    );
  }
);
