import React, {
  useState,
  useEffect,
  forwardRef,
  Ref,
  useCallback,
  useRef,
} from "react";
import { useLoading } from "../../utils/LoadingContext";
import { Category, Book, songIndex } from "../../types";
import { GOOGLE_WEBAPP_URL } from "../../utils/Constants";
import JsonDataTable from "./JsonDataTable";
import {
  useLocalStorageCache,
  doesKeyExistInLocalStorage,
} from "../../utils/useLocalStorageCache";
import { broadcastContent } from "../../utils/sbclientinterface";
import { useSwipeable } from "react-swipeable";

interface SongIndexProps {
  isVisible: Boolean;
  selectedBook: Book | null;
  selectedSong: songIndex | null;
  onNextClick: Function;
  onPrevClick: Function;
  onNavigateBack: Function;
  isFirstSong: boolean;
  isLastSong: boolean;
}

const handleReload = function () {};

const handleShare = function () {};
const handleTransliterate = function () {};
const handleEdit = function () {};

export const SongDetails = forwardRef<HTMLDivElement, SongIndexProps>(
  (
    {
      selectedBook,
      selectedSong,
      isVisible,
      onNextClick,
      onPrevClick,
      isFirstSong,
      isLastSong,
      onNavigateBack,
    },
    ref: Ref<HTMLDivElement>
  ) => {
    const [songVerses, setSongVerses] = useState<any[]>([]);
    const { showLoading, hideLoading } = useLoading();
    const [error, setError] = useState<string | null>(null);

    const [isBroadcasting, setIsBroadcasting] = useState(false);
    const [broadcastStatus, setBroadcastStatus] = useState<
      "success" | "error" | "broadcasting" | "failed" | null
    >(null);
    const [canBroadcast, setCanBroadcast] = useState(true); // Initially can broadcast

    const broadcastContentRef = useRef<HTMLDivElement>(null); // New ref for the broadcastable content

    const localStorageKey: string =
      selectedBook &&
      selectedBook.bookId &&
      selectedSong &&
      selectedSong.songTitle
        ? `songVerses-${selectedBook.bookId}-${selectedSong.songTitle}`
        : "songVerses-no-song"; // Or another suitable default
    const { storedValue: cachedSongVerses, setValue: setCachedSongVerses } =
      useLocalStorageCache<any[]>(localStorageKey);

    const goToNextSong = function () {
      onNextClick();
    };

    const goToPreviousSong = function () {
      if(isFirstSong){ //this is also support swipe navigation behavior
        onNavigateBack(); 
      }
      else{
        onPrevClick();
      }
    };

    const swipeHandlersSongDetails = useSwipeable({
      onSwipedLeft: goToNextSong,
      onSwipedRight: goToPreviousSong,
      trackMouse: true,
      swipeDuration: 500,
      delta: 250, 
    });

    const getButtonClasses = () => {
      let classes = "relative group cursor-pointer";
      if (isBroadcasting) {
        classes += " cursor-not-allowed opacity-50";
      } else if (!canBroadcast) {
        classes += " opacity-50 cursor-not-allowed";
      } else {
        classes += " hover:text-indigo-600";
      }
      return classes;
    };

    const handleBroadcast = useCallback(async () => {
      console.log("here in handleBroadcast");
      if (isBroadcasting || !canBroadcast) {
        console.warn("isBroadcasting or cannot Broadcast");
        return; // Prevent new broadcast if one is in progress or cannot broadcast
      }
      if (broadcastContentRef.current) {
        setIsBroadcasting(true);
        setBroadcastStatus("broadcasting"); // Optional: Visual feedback for 'broadcasting'
        try {
          const htmlToBroadcast = broadcastContentRef.current.innerHTML;
          const result = await broadcastContent(htmlToBroadcast);
          setIsBroadcasting(false);
          setBroadcastStatus("success");
          setCanBroadcast(false); // Disable after successful broadcast
          setTimeout(() => setBroadcastStatus(null), 2000); // Clear success feedback after 2 seconds
        } catch (error: any) {
          console.error("Broadcast failed:", error);
          setIsBroadcasting(false);
          setBroadcastStatus("error");
          setTimeout(() => setBroadcastStatus(null), 3000); // Clear error feedback after 3 seconds
        }
      } else {
        console.warn(
          "broadcastContentRef.current is null. Cannot broadcast content."
        );
        setBroadcastStatus("failed"); // Optional: Update status if ref is invalid
        setIsBroadcasting(false);
      }
    }, [broadcastContentRef, isBroadcasting, canBroadcast]);

    // Enable broadcast when songVerses changes (i.e., new content is loaded)
    useEffect(() => {
      setCanBroadcast(true);
    }, [songVerses]);

    //songtext
    useEffect(() => {
      if (!selectedSong || !selectedBook) return;
      if (selectedSong && selectedBook) {
        const storedItem = localStorage.getItem(
          encodeURIComponent(localStorageKey)
        );
        if (storedItem) {
          try {
            const parsedStoredItem = JSON.parse(storedItem);
            setSongVerses(parsedStoredItem);
            //console.log(`song verses loaded from cache for ${localStorageKey}`);
          } catch (e) {
            fetchSongVersesFromGWebApp(setCachedSongVerses);
          }
        } else {
          fetchSongVersesFromGWebApp(setCachedSongVerses);
        }
      } else {
        setSongVerses([]);
      }
    }, [isVisible, selectedBook, selectedSong, localStorageKey]);

    const fetchSongVersesFromGWebApp = async (
      setCachedSongVerses: (value: any[]) => void
    ) => {
      const abortControllerSongTextGWebApp = new AbortController();
      if (!selectedSong || !selectedBook) {
        return;
      }
      try {
        /*console.log(`bookid:${selectedBook.bookId}`);
        console.log(`sourceRow:${selectedSong.sourceRow}`)
        console.log(`songTitle:${selectedSong.songTitle}`)
        */
        showLoading();
        setSongVerses([[["Loading"]]]);
        const response = await fetch(
          `${GOOGLE_WEBAPP_URL}?mode=api&apiname=getsongtextforsong&book=${selectedBook.bookId}&sourcerow=${selectedSong.sourceRow}&songtitle=${selectedSong.songTitle}`,
          { signal: abortControllerSongTextGWebApp.signal } // Attach its own signal
        );
        if (!response.ok)
          throw new Error(`SongTexts error: ${response.statusText}`);
        const rawData = await response.json();
        //console.log(rawData);
        if (!abortControllerSongTextGWebApp.signal.aborted)
          setSongVerses(rawData);
        setCachedSongVerses(rawData); // Cache the fetched data
        hideLoading();
      } catch (err) {
        if (!abortControllerSongTextGWebApp.signal.aborted) {
          setError(err instanceof Error ? err.message : "Unknown error");
          hideLoading();
        }
      }
    };

    return (
      <div
        className="p-6 max-w-7xl mx-auto"
        style={{ display: isVisible ? "block" : "none" }}
        {...swipeHandlersSongDetails}
      >
        <div className="flex items-center justify-between text-sm text-gray-500 mb-2 hidden md:flex">
          <button
            onClick={(event) =>
              isFirstSong ? onNavigateBack() : goToPreviousSong()
            }
            className={`
              px-4 py-2 rounded-md border border-gray-300
              bg-gray-100 text-gray-700
              hover:bg-gray-200
              focus:outline-none focus:ring-2 focus:ring-blue-500
              transition duration-200
            ${isFirstSong ? "opacity-50 cursor-not-allowed" : "cursor-pointer"}// ... other button styles ...
          `}
          >
            {isFirstSong ? (
              <>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 20 20"
                  fill="currentColor"
                  className="w-5 h-5 inline-block mr-1"
                >
                  <path
                    fillRule="evenodd"
                    d="M12.707 5.293a1 1 0 010 1.414L9.414 10l3.293 3.293a1 1 0 01-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z"
                    clipRule="evenodd"
                  />
                </svg>
                Back to List
              </>
            ) : (
              <>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 20 20"
                  fill="currentColor"
                  className="w-5 h-5 inline-block mr-1"
                >
                  <path
                    fillRule="evenodd"
                    d="M12.707 5.293a1 1 0 010 1.414L9.414 10l3.293 3.293a1 1 0 01-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z"
                    clipRule="evenodd"
                  />
                </svg>
                Previous
              </>            )}
          </button>
          <div className="flex items-center space-x-4">
            <button
              onClick={handleReload}
              aria-label="Reload"
              className="relative group"
            >
              <svg
                className="h-5 w-5 text-gray-400 hover:text-red-600 cursor-pointer"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M12 12l-2-2m0 0l2-2m-2 2l2 2"
                />
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M4 4v5m0 0l1.141-1.143a2 2 0 012.828 0L11 10m0 0l5-5m0 0l1.141 1.143a2 2 0 012.828 0L20 9m-2-7h2m-7-2h2m-5 17h2m-2-7h2m-5-2h2"
                />
              </svg>
              <span className="absolute bottom-full left-1/2 transform -translate-x-1/2 bg-gray-800 text-white text-xs py-1 px-2 rounded-md whitespace-nowrap mb-1 opacity-0 transition-opacity duration-200 group-hover:opacity-100 z-10">
                Reload
              </span>
            </button>
            <button
              onClick={handleBroadcast}
              aria-label="Broadcast"
              className={getButtonClasses()}
              disabled={isBroadcasting || !canBroadcast}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                fill="currentColor"
                className="w-6 h-6"
              >
                <path d="M12 3a9 9 0 1 0 0 18 9 9 0 0 0 0-18zm0 2a7 7 0 1 1 0 14 7 7 0 0 1 0-14zm0 2a5 5 0 1 1 0 10 5 5 0 0 1 0-10zm0 2a3 3 0 1 1 0 6 3 3 0 0 1 0-6zm0 2a1 1 0 1 1 0 2 1 1 0 0 1 0-2z" />
                <path d="M12 1a1 1 0 0 1 1 1v2a1 1 0 1 1-2 0V2a1 1 0 0 1 1-1z" />
                <path d="M12 21a1 1 0 0 1 1-1v-2a1 1 0 1 1-2 0v2a1 1 0 0 1 1 1z" />
                <path d="M21 12a1 1 0 0 1-1 1h-2a1 1 0 1 1 0-2h2a1 1 0 0 1 1 1z" />
                <path d="M3 12a1 1 0 0 1-1 1h2a1 1 0 1 1 0-2H2a1 1 0 0 1 1 1z" />
              </svg>
              <span className="absolute bottom-full left-1/2 transform -translate-x-1/2 bg-gray-800 text-white text-xs py-1 px-2 rounded-md whitespace-nowrap mb-1 opacity-0 transition-opacity duration-200 group-hover:opacity-100 z-10">
                Broadcast
              </span>
            </button>
            <button
              onClick={handleShare}
              aria-label="Share"
              className="relative group"
            >
              <svg
                className="h-5 w-5 text-gray-400 hover:text-indigo-600 cursor-pointer"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                {/* Share Icon */}
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M8.684 13.342C8.886 12.938 9 12.5 9 12c0-.5-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l4.5-4.5m0 0l4.5 4.5m-2.25-2.25l2.25 2.25"
                />
              </svg>
              <span className="absolute bottom-full left-1/2 transform -translate-x-1/2 bg-gray-800 text-white text-xs py-1 px-2 rounded-md whitespace-nowrap mb-1 opacity-0 transition-opacity duration-200 group-hover:opacity-100 z-10">
                Share
              </span>
            </button>
            <button
              onClick={handleTransliterate}
              aria-label="Transliterate"
              className="relative group"
            >
              <svg
                className="h-5 w-5 text-gray-400 hover:text-indigo-600 cursor-pointer"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                {/* Transliterate Icon (you might need to choose a suitable one) */}
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M10 20l4-16m4 4l4-4m-4 4l-4-4m-4 4l-4-4m2 2l4-4m4 4l2-2m-4-4l-4 4"
                />
              </svg>
              <span className="absolute bottom-full left-1/2 transform -translate-x-1/2 bg-gray-800 text-white text-xs py-1 px-2 rounded-md whitespace-nowrap mb-1 opacity-0 transition-opacity duration-200 group-hover:opacity-100 z-10">
                Transliterate
              </span>
            </button>
            <button
              onClick={handleEdit}
              aria-label="Edit"
              className="relative group"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth="1.5"
                stroke="currentColor"
                className="h-6 w-6"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M12 6a3 3 0 11-6 0 3 3 0 016 0zM12 6a3 3 0 11-6 0 3 3 0 016 0zm-3 6v6h3l3-3-3-3h-3z"
                />
              </svg>
              <span className="absolute bottom-full left-1/2 transform -translate-x-1/2 bg-gray-800 text-white text-xs py-1 px-2 rounded-md whitespace-nowrap mb-1 opacity-0 transition-opacity duration-200 group-hover:opacity-100 z-10">
                Edit
              </span>
            </button>
          </div>
          <button
            onClick={goToNextSong}
            className={`
              px-4 py-2 rounded-md border border-gray-300
              bg-gray-100 text-gray-700
              hover:bg-gray-200
              focus:outline-none focus:ring-2 focus:ring-blue-500
              transition duration-200
              ${isLastSong ? "opacity-50 cursor-not-allowed" : "cursor-pointer"}
            `}
            disabled={isLastSong}
            aria-label="Next Song"
          >
            Next
            <svg
              className="h-4 w-4 inline-block ml-1"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M9 5l7 7-7 7"
              />
            </svg>
          </button>
        </div>
        <div ref={broadcastContentRef}>
          <h1 className="text-2xl font-bold text-gray-800 mb-2">
            {selectedBook?.bookId && `${selectedBook.bookId}||`}{" "}
            {selectedSong?.songTitle}
          </h1>
          {songVerses && songVerses[0] && (
            <JsonDataTable
              data={songVerses[0]}
              className="my-4"
              highlightColor="bg-blue-50"
              striped={true}
              hoverEffect={true}
              wrapColumns={[1, 2, 3]} // Wrap 3rd and 4th columns
              maxWidth="max-w-6xl"
            />
          )}
        </div>
      </div>
    );
  }
);
