// hooks/useSongIndices.ts

import { useState, useEffect } from 'react';
import { Category, Book, songIndex } from "../../types";
import { GOOGLE_WEBAPP_URL } from "../../utils/Constants";
import {useLocalStorageCache, doesKeyExistInLocalStorage} from "../../utils/useLocalStorageCache";


const useSongIndices = (selectedBook: Book | null) => {
  const [songIndices, setSongIndices] = useState<songIndex[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<Error | null>(null);
  const localStorageKey:string = selectedBook && selectedBook.bookId ? `songIndices-${selectedBook.bookId}` : 'songIndices-no-book'; // Default key
  //console.log("[useSongIndices]::XXX:localStorageKey:"+localStorageKey);
  const { storedValue: cachedSongIndices, setValue: setCachedSongIndices } = useLocalStorageCache<songIndex[]>(localStorageKey);

  const abortControllerSongsGWebApp = new AbortController();
  const fetchSongsFromGWebApp = async (setCachedSongIndices: (value: songIndex[]) => void) => {
      //console.log(selectedBook);
      if (!selectedBook || !selectedBook.bookId) {
        return;
      }
      const bookId = selectedBook.bookId;
      try {
        //console.log("fetching songindices for "+bookId);
        setSongIndices([{ songTitle: "Loading", sourceRow: -1, tags: "",indexPositionForNavigation:0 }]);
        const response = await fetch(
          `${GOOGLE_WEBAPP_URL}?mode=api&apiname=getsongsforbook&book=${bookId}`,
          { signal: abortControllerSongsGWebApp.signal } // Attach its own signal
        );
        if (!response.ok)
          throw new Error(`SongIndices error: ${response.statusText}`);
        const rawResponse = await response.json();
        //console.log(rawResponse);
        if (!abortControllerSongsGWebApp.signal.aborted) {
          setSongIndices(rawResponse);
          setCachedSongIndices(rawResponse);
        }
      } catch (err) {
        if (!abortControllerSongsGWebApp.signal.aborted) {
            //handle error
        }
      }
    };

  useEffect(() => {
    const fetchSongIndices = async () => {
      setLoading(true);
      setError(null);
      if (selectedBook && selectedBook.bookId) {
      //console.log(`UI request for ${selectedBook.bookId}`);
      //console.log(`localStorageKey:${localStorageKey}`)
      const storedItem = localStorage.getItem(encodeURIComponent(localStorageKey));
      //console.log(`storedItem:${storedItem}`);
      if (storedItem) {
        try {
          const parsedStoredItem = JSON.parse(storedItem);
          if (Array.isArray(parsedStoredItem)) {
            //console.log("why?");
            //console.log(`parsedStoredItem:${JSON.stringify(parsedStoredItem)}`);
            setSongIndices(parsedStoredItem);
            //console.log(`Song indices for book ${selectedBook.bookId} with storagekey ${localStorageKey} loaded from cache.`);
          } else {
            //console.log(`//fetch if stored item is not an array.`)
            fetchSongsFromGWebApp( setCachedSongIndices); //fetch if stored item is not an array.
          }
        } catch (e) {
          //console.log(`//fetch if stored item is not valid JSON.`)
          fetchSongsFromGWebApp( setCachedSongIndices); //fetch if stored item is not valid JSON.
        }
      } else {
        //console.log(`fetch if nothing in local storage.`)
        fetchSongsFromGWebApp( setCachedSongIndices); //fetch if nothing in local storage.
      }
      }
     else {
        setSongIndices([]);
        localStorage.removeItem("songIndices-no-book");
        setLoading(false);
      }
    };
    fetchSongIndices();
  }, [selectedBook,localStorageKey]);

  return { songIndices, loading };
};

export default useSongIndices;