import React, { useState, useRef, Suspense, useEffect } from "react";
import { Category, Book, songIndex } from "./types";
import { Categories, Books } from "./appobjects/CategoriesAndBooks";
import { SongIndices } from "./appobjects/SongIndices";
import { SongDetails } from "./appobjects/songtext/SongDetails";
import ReadingTogether from "./appobjects/songtext/ReadingTogether";
import { Search } from "./appobjects/Search";
import useSongIndices from "./appobjects/hooks/useSongIndices";
import { useSwipeable } from 'react-swipeable';


interface ScrollPositionData {
  scrollTop: number;
  topVisibleId: string | null;
}
type ViewName =
  | "categories"
  | "books"
  | "songs"
  | "songDetail"
  | "search"
  | "readTogether";

type ScrollPositionsState = {
  [K in ViewName]?: ScrollPositionData;
};

const HierarchicalSacredTextsApp: React.FC = () => {
  //app view

  const [currentView, setCurrentView] = useState<ViewName>("categories");
    //user selections
  const [selectedCategoriesAndBooks, setSelectedCategoriesAndBooks] =
    useState<Category | null>(null);
  const [selectedBook, setSelectedBook] = useState<Book | null>(null);
  const { songIndices } = useSongIndices(selectedBook);
  const [selectedSong, setSelectedSong] = useState<songIndex | null>(null);

  //user scroll position
  const [scrollPositions, setScrollPositions] = useState<ScrollPositionsState>(
    {}
  );

  const bookRef = useRef<HTMLDivElement | null>(null);
  const songIndexRef = useRef<HTMLDivElement | null>(null);
  const songDetailsRef = useRef<HTMLDivElement | null>(null);
  //const songDetailsRef = useRef<{ scrollTop: () => number; setScrollTop: (value: number) => void } | null>(null);

  //component showhide methods
  const [showCategory, setShowCategory] = useState(true);
  const [showBooks, setShowBooks] = useState(false);
  const [showSongIndices, setShowSongIndices] = useState(false);
  const [currentSongPosition, setCurrentSongPosition] = useState<number>(0);

  const [showSongDetail, setShowSongDetail] = useState(false);
  const [showReadTogether, setReadTogether] = useState(false);

  const [searchQuery, setSearchQuery] = useState<string>("ज्ञान्देव");
  //const [favorites, setFavorites] = useState<any[]>([]);
  const [error] = useState<string | null>(null);

  const isFirstSong = currentSongPosition === 0;
  const isLastSong = currentSongPosition === songIndices.length - 1;

  const saveScrollPosition = (
    viewName: ViewName,
    ref:
      | React.RefObject<HTMLDivElement | null>
      | React.RefObject<{
          scrollTop: () => number;
          setScrollTop: (value: number) => void;
        } | null>
  ) => {
    if (ref.current) {
      let scrollTop: number | undefined;
      if ((ref.current as { scrollTop?: number }).scrollTop !== undefined) {
        scrollTop = (ref.current as HTMLDivElement).scrollTop;
      } else if (
        (ref.current as { scrollTop?: () => number }).scrollTop !== undefined
      ) {
        scrollTop = (ref.current as { scrollTop: () => number }).scrollTop();
      }

      let topVisibleId: string | null = null;
      if (scrollTop !== undefined && (ref.current as HTMLDivElement).children) {
        const children = Array.from(
          (ref.current as HTMLDivElement).children
        ) as HTMLElement[];
        for (const child of children) {
          if (child.id && child.offsetTop >= scrollTop) {
            topVisibleId = child.id;
            break;
          }
        }
      } else if (
        scrollTop !== undefined &&
        viewName === "songDetail" &&
        (ref.current as { scrollTop?: () => number }).scrollTop !== undefined
      ) {
        // If it's songDetail, we can't rely on children for topVisibleId easily
        // You might need a different logic here if you want to save a specific element ID
        // within the JsonDataTable's scrollable area. For now, we'll leave it as null.
      }

      if (scrollTop !== undefined) {
        setScrollPositions((prev) => ({
          ...prev,
          [viewName]: { scrollTop, topVisibleId },
        }));
      }
    }
  };

  const restoreScrollPosition = (
    viewName: ViewName,
    ref:
      | React.RefObject<HTMLDivElement | null>
      | React.RefObject<{
          scrollTop: () => number;
          setScrollTop: (value: number) => void;
        } | null>
  ) => {
    const savedPosition = scrollPositions[viewName];
    if (savedPosition && ref.current) {
      if (
        viewName === "songDetail" &&
        (ref.current as { setScrollTop?: (value: number) => void }).setScrollTop
      ) {
        // If it's songDetail, use the custom setScrollTop
        (ref.current as { setScrollTop: (value: number) => void }).setScrollTop(
          savedPosition.scrollTop
        );
      } else if ((ref.current as HTMLDivElement).scrollTop !== undefined) {
        // For other views (which are likely divs), use scrollTop directly
        if (savedPosition.topVisibleId) {
          const elementToScrollTo = document.getElementById(
            savedPosition.topVisibleId
          );
          if (elementToScrollTo) {
            (ref.current as HTMLDivElement).scrollTop =
              elementToScrollTo.offsetTop;
          } else {
            (ref.current as HTMLDivElement).scrollTop = savedPosition.scrollTop; // Fallback
          }
        } else {
          (ref.current as HTMLDivElement).scrollTop = savedPosition.scrollTop;
        }
      }
    }
  };

  const getCurrentRef = (
    viewName: ViewName
  ):
    | React.RefObject<HTMLDivElement | null>
    | React.RefObject<{
        scrollTop: () => number;
        setScrollTop: (value: number) => void;
      } | null> => {
    switch (viewName) {
      case "books":
        return bookRef;
      case "songs":
        return songIndexRef;
      case "songDetail":
        return songDetailsRef;
      default:
        return { current: null };
    }
  };

  /*
  const getPreviousView = (currentView: ViewName): ViewName => {
    if (currentView === "books") return "categories";
    if (currentView === "songs") return "books";
    if (currentView === "songDetail") return "songs";
    if (currentView === "search") return "categories";
    if (currentView === "readTogether") return "categories";
    return "categories";
  };
  */

  const handleClickBrowseScreen = () => {
    setCurrentView("categories");
    setShowCategory(true);
    setShowBooks(false);
    setShowSongIndices(false);
    setShowSongDetail(false);
    setReadTogether(false);
  };
  const handleClickSearchScreen = () => {
    setCurrentView("search");
    setShowCategory(false);
    setShowBooks(false);
    setShowSongIndices(false);
    setShowSongDetail(false);
    setReadTogether(false);
  };
  const handleClickReadTogether = () => {
    setCurrentView("readTogether");
    setReadTogether(true);
    setShowCategory(false);
    setShowBooks(false);
    setShowSongIndices(false);
    setShowSongDetail(false);
  };
  const handleCategorySelect = (category: Category) => {
    setSelectedCategoriesAndBooks(category);
    setShowCategory(false);
    setShowBooks(true);
    setShowSongIndices(false);
    setShowSongDetail(false);
    setCurrentView("books");
  };

  const handleBookSelect = (book: Book) => {
    saveScrollPosition(currentView, getCurrentRef(currentView));
    // Reset scroll position for 'songs' by removing it from the state
    setScrollPositions((prev) => {
      const { songs, ...rest } = prev;
      return rest;
    });
    const songsRef = getCurrentRef("songs");
    if (songsRef && songsRef.current) {
      songsRef.current.scrollTop = 0;
    }
    setSelectedBook(book);
    setShowCategory(false);
    setShowBooks(false);
    setShowSongIndices(true);
    setShowSongDetail(false);

    setCurrentView("songs");
    //restoreScrollPosition("songs", songIndexRef);
  };

  const handleNextSongClick = () => {
    if (!isLastSong) {
      const nextPosition = currentSongPosition + 1;
      setCurrentSongPosition(nextPosition);
      setSelectedSong(songIndices[nextPosition]);
    }
  };

  const handlePrevSongClick = () => {
    //console.log(`handlePrevSongClick::currentSongPosition:${currentSongPosition}`)
    if (!isFirstSong) {
      const prevPosition = currentSongPosition - 1;
      setCurrentSongPosition(prevPosition);
      setSelectedSong(songIndices[prevPosition]);
      //console.log(`${JSON.stringify(songIndices[prevPosition])}`)
    } else {
      console.warn(`handleNextSongClick::skipping for ${currentSongPosition} `);
    }
  };
  const handleSongSelect = (songindex: songIndex) => {
    //console.log(`currentView:${currentView}`)
    saveScrollPosition(currentView, getCurrentRef(currentView));
    // Reset scroll position for 'songDetail' by removing it from the state

    setCurrentView("songDetail");
    setSelectedSong(songindex);
    if (
      songindex?.indexPositionForNavigation != null &&
      songindex.indexPositionForNavigation >= 0
    ) {
      setCurrentSongPosition(songindex.indexPositionForNavigation);
      //console.log(`indexPositionForNavigation:${songindex.indexPositionForNavigation}`);
    }
    //console.log(`songIndex:${JSON.stringify(songindex)}`);
    setShowCategory(false);
    setShowBooks(false);
    setShowSongIndices(false);
    setShowSongDetail(true);
    setScrollPositions((prev) => {
      const { songDetail, ...rest } = prev;
      return rest;
    });
    //const songDetailRef = getCurrentRef('songDetail');
    if (songDetailsRef.current && songDetailsRef.current.scrollTop) {
      songDetailsRef.current.scrollTop = 0;
    }

    //restoreScrollPosition("songDetail", songDetailsRef);
  };

  const handleBackToSong = () => {
    if (currentSongPosition !== null && songIndices[currentSongPosition]) {
      saveScrollPosition(currentView, getCurrentRef(currentView));
      const lastViewedSong = songIndices[currentSongPosition];
      setSelectedSong(lastViewedSong);
      setShowSongDetail(true);
      setCurrentView("songDetail");
      setShowCategory(false);
      setShowBooks(false);
      setShowSongIndices(false);
      setShowSongDetail(true);
      setScrollPositions((prev) => {
        const { songDetail, ...rest } = prev;
        return rest;
      });
    }
  };

  /*
  const handleBack = () => {
    const previousView = getPreviousView(currentView);
    saveScrollPosition(currentView, getCurrentRef(currentView));
    setCurrentView(previousView);
    restoreScrollPosition(previousView, getCurrentRef(previousView));
  };
*/
  const handleSwipeLeftGlobal = () => {
    console.log(`handleSwipeLeftGlobal::`);
    navigateBack(); // Go back in browser history
};

const handleSwipeRightGlobal = () => {
  console.log(`handleSwipeRightGlobal::`);
  if (currentSongPosition !== null && songIndices[currentSongPosition]) {
      const lastViewedSong = songIndices[currentSongPosition];
      setSelectedSong(lastViewedSong);
      setShowSongDetail(true);
      // or navigate to the song detail route if using routing
  }
};

const swipeHandlersGlobal = useSwipeable({
  onSwipedLeft: !showSongDetail ? () => {
      //console.log("onSwipedLeft callback triggered");
      handleSwipeRightGlobal();
  } : undefined,
  onSwipedRight: !showSongDetail ? () => {
      //console.log("onSwipedRight callback triggered");
      handleSwipeLeftGlobal();
  } : undefined,
  trackMouse: true,
  swipeDuration: 500,
  delta: 250,
});
  const navigateBack = () => {
    if (currentView === "books") {
      setCurrentView("categories");
      setShowCategory(true);
      setShowBooks(false);
      setShowSongIndices(false);
      setShowSongDetail(false);
      setSelectedBook(null);
    } else if (currentView === "songs") {
      setCurrentView("books");
      setShowCategory(false);
      setShowBooks(true);
      restoreScrollPosition("books", bookRef);
      setShowSongIndices(false);
      setShowSongDetail(false);
      setSelectedBook(null);
      //setSongIndices([]);
    } else if (currentView === "songDetail") {
      saveScrollPosition("songDetail", songDetailsRef);
      setCurrentView("songs");
      setShowSongIndices(true);
      setShowSongDetail(false);
      setSelectedSong(null);
      restoreScrollPosition("songs", songIndexRef);
    } else if (currentView === "search") {
      setCurrentView("categories");
      setShowCategory(true);
    } else if (currentView === "readTogether") {
      setCurrentView("categories");
      setShowCategory(true);
      setReadTogether(false);
      setShowBooks(false);
      setShowSongIndices(false);
      setShowSongDetail(false);
    }
  };

  const renderContent = () => {
    if (error) return <div>Error loading categories: {error}</div>;
    return (
      <div {...swipeHandlersGlobal} id="sathyaDebug">
        <Categories isVisible={showCategory} onSelect={handleCategorySelect} />
        <Books
          ref={bookRef}
          isVisible={showBooks}
          onSelect={handleBookSelect}
          selectedCategoriesAndBooks={selectedCategoriesAndBooks}
        />
        {
        <SongIndices
          ref={songIndexRef}
          isVisible={showSongIndices}
          onSelect={handleSongSelect}
          onSwipeLeft={navigateBack}
          onSwipeRight={handleBackToSong}
          songIndices={songIndices}
          selectedBook={selectedBook}
          currentSongPosition={currentSongPosition}
        />
        }
        {/*selectedBook && (
        <SongIndexProvider selectedBookProp={selectedBook}>
          <SongIndices
            ref={songIndexRef}
            isVisible={showSongIndices}
            onSelect={handleSongSelect}
            onSwipeLeft={navigateBack}
            onSwipeRight={handleBackToSong}
            currentSongPosition={currentSongPosition}
          />
        </SongIndexProvider>
      )*/}        
        <SongDetails
          ref={songDetailsRef}
          isVisible={showSongDetail}
          selectedBook={selectedBook}
          selectedSong={selectedSong}
          onNextClick={handleNextSongClick}
          onPrevClick={handlePrevSongClick}
          onNavigateBack={navigateBack}
          isFirstSong={isFirstSong} // Pass this prop
          isLastSong={isLastSong} // Pass this prop
          key={selectedBook?.bookId || selectedSong?.songTitle}
        />
        <div style={{ display: currentView === "search" ? "block" : "none" }}>
          <Search />
        </div>
        <Suspense fallback={<div>Loading Reading Together...</div>}>
          {showReadTogether && <ReadingTogether broadcastId={1} />}
        </Suspense>
      </div>
    );
  };

  /*
  useEffect(() => {
    console.log(`useEffect::selectedBook+songIndices`)
    if (songIndices.length > 0) {
      setSelectedSong(songIndices[0]);
      setCurrentSongPosition(0);
    } else {
      setSelectedSong(null);
      setCurrentSongPosition(-1);
    }
    setShowSongDetail(selectedBook !== null && songIndices.length > 0);
  }, [selectedBook, songIndices]);
*/
  return (
    <div className="flex flex-col h-screen bg-gray-100">
      {/* Header */}
      <header className="bg-indigo-700 text-white py-2 sm:py-4 px-2 sm:px-4 shadow-md sticky top-0 z-10">
        <div className="flex justify-between items-center">
          <h1 className="text-lg sm:text-xl font-bold">Sacred Texts App</h1>
          <button
            onClick={() => setCurrentView("search")}
            className="p-1 sm:p-2 rounded-full bg-indigo-600 text-s m sm:text-base"
          >
            🔍
          </button>
        </div>
      </header>

      {/* Search Bar - Shows when search is active */}
      {currentView === "search" && (
        <div className="p-4 bg-white shadow-md">
          <div className="flex">
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder="Search texts, mantras, songs..."
              className="flex-1 p-3 border border-gray-300 rounded-l"
            />
            <button className="bg-indigo-600 text-white p-3 rounded-r">
              Search
            </button>
          </div>
        </div>
      )}

      {/* Navigation Breadcrumb */}
      {currentView !== "categories" && (
        <div>
          <nav
            //className="bg-white p-3 shadow-sm flex items-center space-x-2 text-sm overflow-x-auto"
            className="bg-white p-3 shadow-sm flex items-center justify-between text-sm overflow-x-auto"
            aria-label="breadcrumb"
          >
            <div>
              <button
                onClick={navigateBack}
                className="text-indigo-600 hover:underline"
                aria-label="Go to previous page"
              >
                <svg
                  className="h-4 w-4 inline-block mr-1"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M7 16l-4-4m0 0l4-4m-4 4h18"
                  />
                </svg>
                Back
              </button>
              {selectedCategoriesAndBooks && (
                <>
                  <span className="text-gray-500">/</span>
                  <button
                    onClick={() => {
                      /* Navigate to category logic */
                    }}
                    className="text-gray-700 hover:underline cursor-pointer"
                  >
                    {selectedCategoriesAndBooks.name}
                  </button>
                </>
              )}
              {selectedBook && (
                <>
                  <span className="text-gray-500">/</span>
                  <span className="font-medium text-gray-900">
                    {selectedBook.bookName}
                  </span>
                </>
              )}
              {selectedSong && (
                <>
                  <span className="text-gray-500">/</span>
                  <span className="font-medium text-gray-900">
                    {selectedSong.songTitle}
                  </span>
                </>
              )}
            </div>
            {!showSongDetail &&
              currentSongPosition !== null &&
              songIndices[currentSongPosition] && (
                <button
                  onClick={handleBackToSong}
                  className="text-indigo-600 hover:underline"
                  aria-label={`Go back to ${songIndices[currentSongPosition]?.songTitle}`}
                >
                  Back to Song
                </button>
              )}
          </nav>
        </div>
      )}

      {/* Main Content */}
      <main className="flex-1 overflow-auto p-4">{renderContent()}</main>

      {/* Bottom Navigation */}
      <nav className="bg-white border-t border-gray-200 p-3">
        <div className="flex justify-around">
          <button
            onClick={() => handleClickBrowseScreen()}
            className={`flex flex-col items-center ${
              currentView === "categories" ? "text-indigo-600" : "text-gray-500"
            }`}
          >
            <span className="text-2xl">📜</span>
            <span className="text-xs">Browse</span>
          </button>
          <button
            onClick={() => handleClickSearchScreen()}
            className={`flex flex-col items-center ${
              currentView === "search" ? "text-indigo-600" : "text-gray-500"
            }`}
          >
            <span className="text-2xl">🔍</span>
            <span className="text-xs">Search</span>
          </button>
          <button
            onClick={() => handleClickReadTogether()}
            className={`flex flex-col items-center ${
              currentView === "readTogether"
                ? "text-indigo-600"
                : "text-gray-500"
            }`}
          >
            <span className="text-2xl">🔯</span>
            <span className="text-xs">Sankeertan</span>
          </button>
          <button className="flex flex-col items-center text-gray-500">
            <span className="text-2xl">⚙️</span>
            <span className="text-xs">Settings</span>
          </button>
        </div>
      </nav>
    </div>
  );
};

export default HierarchicalSacredTextsApp;
