import { useState, useEffect, useRef } from 'react';

// Helper function for encoding keys
const encodeLocalStorageKey = (key: string): string => encodeURIComponent(key);

// Helper function to check if a key exists in local storage
const doesKeyExistInLocalStorage = (key: string): boolean => {
  const encodedKey = encodeLocalStorageKey(key);
  try {
    return localStorage.getItem(encodedKey) !== null;
  } catch (error) {
    console.error('Error checking local storage:', error);
    return false;
  }
};

function useLocalStorageCache<T>(key: string, initialValue: T | null = null): {
  storedValue: T | null;
  setValue: (newValue: T | null) => void;
} {
  const encodedKey = encodeLocalStorageKey(key);
																  

  const [storedValue, setStoredValue] = useState<T | null>(() => {
    try {
      const item = window.localStorage.getItem(encodedKey);
      //console.log(`[useLocalStorageCache] Key: ${encodedKey}, Raw Item:`, item); // Crucial log
      return item ? JSON.parse(item) : initialValue;
    } catch (error) {
      console.error('Error retrieving from local storage:', error);
      return initialValue;
    }
  
	  
  });

  const isInitialRender = useRef(true);
  const isSetValueCalled = useRef(false);

  useEffect(() => {
    if (isInitialRender.current) {
      isInitialRender.current = false;
      return;
    }
    if (isSetValueCalled.current && storedValue !== null) {
      //console.log(`[useLocalStorageCache] Setting Key: ${encodedKey}, Value:`, storedValue);
      try {
        //console.log("setting item for [" + encodedKey + "]");
        window.localStorage.setItem(encodedKey, JSON.stringify(storedValue));
      } catch (error) {
        console.error('Error saving to local storage:', error);
      }
    }
    isSetValueCalled.current = false;
  }, [encodedKey, storedValue]);

  const setValue = (newValue: T | null) => {
    isSetValueCalled.current = true;
    setStoredValue(newValue);
  };

  return { storedValue, setValue };
}

export { useLocalStorageCache, doesKeyExistInLocalStorage };